  const toBase64 = file => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });

  const uploadRetrato = async e => {
    if (!e.target.files || e.target.files.length === 0) {
      setRetrato(undefined)
      return
    }
    setRetrato(await toBase64(e.target.files[0]))
  }

  // const selecionarExemplo = (index) => {
  // }

  const getDV = () => (new Promise((resolve, reject) => {
    const requestParam = {
      method: 'POST',
      url: 'https://gateway.apiserpro.serpro.gov.br/datavalid-demonstracao/v2/validate/pf-face',
      headers: {
        'content-type': 'application/json',
        'Authorization': 'Bearer 06aef429-a981-3ec5-a1f8-71d38d86481e'
      },
      body: payloadReq
    }

    request(requestParam, (error, response, body) => {
      if (error)
        reject(error);
      else {
        if (response.statusCode !== 200) {
          reject('PF-FACE <' + response.statusCode + ' - ' + body + '>');
        }
        setPayloadRes(JSON.stringify(JSON.parse(body), null, 2))
        resolve(body);
      }
    });
  }));


